.container{
    width: 300px;
    background-color: rgb(86, 219, 86);
    text-align: center;
    border-radius: 11px;
    margin-top: 22px;
    padding: 22px;
    margin: auto;
    margin-top: 55px;
}
.container p{
    font-size: 66px;
}
.container button{
    width: 90px;
    height: 33px;
    background-color: rgb(8, 156, 206);
    color: white;
    font-size: 20px;
    margin-inline: 4px;
    border: none;
    cursor: pointer;
}